</template>

<script>
import first from '../pages/echarts/first'
import second from '../pages/echarts/second'
import third from '../pages/echarts/third'
import icon from '../assets/icon/icon.css'
import { getAllWords,getAllExcel,getAllPpt,getAllUser} from '../api/index'
export default {
    components:{
        first,
        second,
        third
   },
     data() {
        return {
            tableData: [],
            tableLabel: {
                name: '素材分类',
                todayAdd: '今日添加',
                monthBuy: '总数量',
                totalBuy: '总购买'
            },
            ContsLength:[
                {name:"PowerPoint",value:0,type:1,className:"ppt-box"},
                {name:"Excel",value:0,type:2,className:"excel-box"},
                {name:"Word",value:0,type:3,className:"word-box"},
                {name:"用户数",value:0,type:4,className:"user-box"},
                ],
            pastYearMonths:[],  // 存放过去12个月的年月
            lastdays:[] //过去30天
        }
     },
     created(){
        this.lastmonths()
        this.last30days()
        this.getData()
     },
     methods:{
        getData () {
            getAllWords().then(res => {
                this.ContsLength[2].value=res.length
            })
            getAllExcel().then(res => {
                this.ContsLength[1].value=res.length
            })
            getAllPpt().then(res => {
                this.ContsLength[0].value=res.length
            })
            getAllUser().then(res => {
                this.ContsLength[3].value=res.length
            })
        },
        toPage(val){
            if(val===1){
                this.$router.push( `/pptList`)
            }else if(val===2){
                this.$router.push( `/excetList`)
            }else if(val===3){
                this.$router.push( `/wordList`)
            }else if(val===4){
                this.$router.push( `/userList`)
            }
            
        },
        lastmonths(){
            const today = new Date();
            const currentYear = today.getFullYear();
            const currentMonth = today.getMonth() + 1; // 因为getMonth()返回的是0-11，需要加1
            
            // 将当前年月作为第一个元素插入数组
            this.pastYearMonths.push(`${currentYear}-${String(currentMonth).padStart(2, '0')}`);
            // 构造过去12个月的年月数组
            for (let i = 1; i < 12; i++) {
            let year = currentYear;
            let month = currentMonth - i;
            if (month <= 0) {
                // 如果月份小于等于0，则说明需要向前一年
                year = currentYear - 1;
                month += 12;   // 这里加上12，相当于将月份转化为借位的形式
            }
            // 将年月格式化为"YYYY-MM"的形式，并插入数组
            this.pastYearMonths.push(`${year}-${String(month).padStart(2, '0')}`);  
            }
            this.pastYearMonths=this.pastYearMonths.reverse()
        },
        last30days(){
            // 获取当前日期
            let today = new Date();

            // 创建存储日期字符串的数组
            let dates = [];

            // 循环生成前30天的日期字符串并将其添加到数组中
            for (let i = 0; i < 30; i++) {
            let date = new Date(today);
            date.setDate(today.getDate() - i);
            let dateString = date.toISOString().slice(0, 10);
            dates.push(dateString);
            }
            this.lastdays=dates.reverse()
            // 输出日期数组
            //console.log(dates);
        }
     }
}
</script>

